@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap");

html,
body {
 margin: 0;
 padding: 0;
}

body {
 font-family: "Roboto Condensed", sans-serif;
}

.container {
 margin: 10px 20px;
}

.blog {
 font-size: 20px;
 margin: 20px 50px;
}

.blog h1 {
 margin-bottom: 0;
 font-size: 100px;
}

.feedback {
 padding: 50px;
}

a {
 color: black;
 text-decoration: none;
}

.middle {
 position: absolute;
 top: 50%;
 left: 50%;
 transform: translate(-50%, -50%);
 text-align: center;
 font-size: 25px;
}

.nav {
 background-color: #162e49d0;
 overflow: hidden;
 margin: 0;
 padding: 0px 400px;
 position: -webkit-sticky;
}

.nav h1 {
 padding: 10px;
 float: left;
 color: white;
}

.nav img {
 float: left;
 border-radius: 20%;
 padding: 10px;
 width: auto;
 height: 80px;
}

.nav a {
 float: right;
 color: gray;
 text-align: center;
 padding: 30px 30px 30px 15px;
 text-decoration: none;
 font-size: 30px;
}

.nav a:hover {
 color: rgb(236, 230, 230);
}

.nav a.active {
 color: rgb(255, 255, 255);
}

.footer {
 padding: 5px 15px;
 color: black;
 position: absolute;
 bottom: 0;
}
